t\"\r\n               :key=\"index\"\r\n               @click=\"acitivIndex=index,getCompanyList(item.id)\">{{item.name}}</div>\r\n        </div>\r\n        <div class=\"main_item\">\r\n          <!-- <div>\r\n            <el-row :gutter=\"30\">\r\n\r\n              <el-col :span=\"24\">\r\n                <div v-for=\"(item,index) in list\"\r\n                     :key=\"index\">\r\n                  \r\n                </div>\r\n              </el-col>\r\n\r\n            </el-row>\r\n          </div> -->\r\n          <el-table :data=\"list\"\r\n                    v-if=\"currentProvince\"\r\n                    @selection-change=\"handleSelectionChange\">\r\n            <el-table-column type=\"selection\"\r\n                             width=\"55\">\r\n            </el-table-column>\r\n            <el-table-column label=\"保险公司图片\"\r\n                             align='center'>\r\n              <template slot-scope=\"scope\">\r\n                <img :src=\"scope.row.photo\"\r\n                     class=\"photo\" />\r\n              </template>\r\n            </el-table-column>\r\n            <el-table-column label=\"保险公司\"\r\n                             align=\"center\">\r\n              <template slot-scope=\"scope\">\r\n                {{scope.row.company}}\r\n\r\n              </template>\r\n            </el-table-column>\r\n\r\n            <el-table-column label=\"卡数量\"\r\n                             align=\"center\">\r\n              <template slot-scope=\"scope\">\r\n                <el-input placeholder=\"请输入卡数量\"\r\n                          v-model=\"scope.row.card_num\" />\r\n              </template>\r\n            </el-table-column>\r\n            <el-table-column label=\"总卡数\"\r\n                             align=\"center\"\r\n                             prop=\"num\">\r\n              <template slot-scope=\"scope\">\r\n                {{scope.row.num}}张\r\n              </template>\r\n            </el-table-column>\r\n            <el-table-column label=\"消耗数量\"\r\n                             align=\"center\"\r\n                             prop=\"num\">\r\n              <template slot-scope=\"scope\">\r\n                {{scope.row.consume}}张\r\n              </template>\r\n            </el-table-column>\r\n            <el-table-column label=\"剩余数量\"\r\n                             align=\"center\"\r\n                             prop=\"num\">\r\n              <template slot-scope=\"scope\">\r\n                {{scope.row.sur_num}}张\r\n              </template>\r\n            </el-table-column>\r\n            <el-table-column label=\"操作\"\r\n                             align=\"center\">\r\n              <template slot-scope=\"scope\">\r\n                <el-button type=\"danger\"\r\n                           size=\"small\"\r\n                           @click=\"remove(scope.row)\">删除</el-button>\r\n                <el-button type=\"primary\"\r\n                           size=\"small\"\r\n                           @click=\"status=1,Modify(scope.row)\">修改</el-button>\r\n              </template>\r\n            </el-table-column>\r\n          </el-table>\r\n          <el-button type=\"primary\"\r\n                     v-if=\"checkedCompany && checkedCompany.length>0\"\r\n                     class=\"main_confirm\"\r\n                     @click=\"Export()\">导出</el-button>\r\n          <!-- <el-row>\r\n            <el-col :span=\"1\">\r\n              <el-checkbox v-model=\"item.checked\"></el-checkbox>\r\n            </el-col>\r\n            <el-col :span=\"2\">\r\n              <el-input class=\"clear-border\"\r\n                        v-model=\"item.company\"\r\n                        @change=\"(value)=>singleModify(value,item)\" />\r\n            </el-col>\r\n            <el-col :span=\"2\"\r\n                    :offset=\"1\">\r\n              <el-input class=\"clear-border\"\r\n                        v-model=\"item.sur_num\"\r\n                        @change=\"(value)=>singleModify(value,item)\"